
.chat-header
  display: flex
  justify-content: space-between
  align-items: center
  padding: 2px 16px
  background-color: var(--color-primary)
  color: white
  position: sticky
  top: 0
  z-index: 10

.header-left
  display: flex
  align-items: center

.header-actions
  display: flex
  align-items: center
  gap: 8px

.header-button
  background: none
  border: none
  color: white
  padding: 4px
  cursor: pointer
  display: flex
  align-items: center
  justify-content: center
  &:hover
    opacity: 0.8

.header-divider
  color: rgba(255, 255, 255, 0.5)
  margin: 0 8px

.custom-control-input
  cursor: pointer

.custom-control-label
  cursor: pointer
  font-size: 14px

.credit-display
  display: flex
  align-items: center
  gap: 6px
  padding: 4px 8px
  border-radius: var(--border-radius-sm)
  cursor: pointer
  transition: opacity 0.2s
  font-size: 14px
  font-weight: 500

  &:hover
    opacity: 0.8

  .credit-icon
    width: 16px
    height: 16px

    &.mdi-crown
      font-size: 1.25rem
      display: inline-flex
      align-items: center
      color: gold

  .credit-text
    color: white

  &.credit-success
    background-color: var(--color-success)

  &.credit-warning
    background-color: var(--color-warning)

  &.credit-exhausted
    background-color: var(--color-danger)
  margin-bottom: 0

.custom-control-label::before
  border-color: white !important
  border-width: 1px
  border-style: solid

.custom-control-input:checked ~ .custom-control-label::before
  border-color: white !important
  border-width: 1px
  border-style: solid

.large-icon
  font-size: 24px
