
.help-wrapper
  display: inline
  position: relative
  &.highlighted
    text-decoration: underline
    text-decoration-color: var(--color-text-help)
  .text
    display: inline
    &:hover
      cursor: pointer

.widget-wrapper
  $widget-width: 360px
  $x-breakpoint: #{$widget-width + 20px}
  $x-breakpoint-2: #{$widget-width * 2 + 20px}
  position: relative
  left: .1rem
  top: .1rem
  width: 1rem
  height: 1rem
  display: inline-block

  .question-mark
    display: flex
    color: var(--color-text-help)
    transition: all .2s ease-out
    height: 1rem
    &:hover
      cursor: pointer

    svg
      width: 1rem
      height: 1rem

  .widget
    width: $widget-width
    z-index: 10
    position: absolute
    box-shadow: 0 0 0 9999px rgb(0 0 0 / 0.5)
    border-radius: 12px
    background-color: var(--color-surface-primary)
    display: flex
    flex-direction: column
    color: var(--color-text-primary)

    .logo-icon
      max-height: 50px
      object-fit: contain
      border-radius: 8px
      margin: 0px 15px

    @media (min-width: $x-breakpoint-2)
      &.left
        left: 100%
      &.right
        right: 100%
      &.bottom
        bottom: 100%
      &.top
        top: 100%

    @media (max-width: $x-breakpoint-2)
      left: 0rem
      right: 0rem
      top: 5rem
      margin: auto
      position: fixed

    @media (max-width: $x-breakpoint)
      left: 1rem
      right: 1rem
      width: auto

    .close-button
      $close-btn-height: 24px
      position: absolute
      left: #{$close-btn-height * -.5}
      top: #{$close-btn-height * -.5}
      color: var(--color-text-primary)
      transition: all .2s ease-out
      &:hover
        cursor: pointer
        opacity: .7

      svg
        width: $close-btn-height
        height: $close-btn-height

    .content
      padding: 1rem .875rem
      font-size: 14px
      font-weight: normal

      & > *
        margin-bottom: 1em
        display: block

      h1, h2, h3
        font-weight: bold

      h1
        font-size: 1.3em
      h2
        font-size: 1.2em
      h3
        font-size: 1.1em

      ul
        padding-left: 2em

      .video-wrapper
        position: relative
        padding-bottom: 56.25%
        height: 0

        iframe
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
