
.collapse-toggle
  width: 100%
  font-weight: bold
  display: flex
  align-items: center
  justify-content: center
  gap: .5rem
  background: none
  border: none
  color: var(--color-text-primary)
  margin-top: .5rem
  svg
    transition: all .3s ease-in-out
    &.rotated
      transform: rotate(180deg)
