
.chat-container
  display: flex
  flex-direction: column
  height: 100%
  overflow-y: auto

.chat-messages
  flex-grow: 1
  overflow-y: auto
  padding: 1rem 0.5rem
  display: flex
  flex-direction: column

.message
  max-width: 80%
  margin-bottom: 1.5rem
  padding: 0.5rem
  border-radius: 18px
  line-height: 1.4
  position: relative
  color: black

  &.user
    align-self: flex-end
    background-color: #dcf8c6

  &.assistant
    align-self: flex-start
    background-color: #fff
    display: flex
    align-items: flex-start

.assistant-avatar
  width: 30px
  height: 30px
  background-color: #8e44ad
  border-radius: 50%
  display: flex
  align-items: center
  justify-content: center
  margin-right: 8px
  flex-shrink: 0

  i
    color: white

