

.full-screen-height
  height: calc(100vh - 50px - var(--gap-between-blocks))
  margin-bottom: -120px

.wrapper
  display: flex
  flex-direction: column
  align-items: center
  gap: .825rem
  padding-bottom: 1rem
  position: relative
  width: 100%

.feedWrap
  display: flex
  flex-direction: column
  flex: 1
  padding: 10px
  width: 100%
  background-color: var(--color-background)
  border: solid var(--color-surface-primary) 1px
.feedWrap::-webkit-scrollbar
  background-color: var(--color-background)
.feedWrap::-webkit-scrollbar-thumb
  background-color: var(--color-surface-primary)

.inputWrap
  display: flex
  flex-direction: row
  justify-content: space-between
  height: 50px
  width: 100%
  gap: 1rem

.textInput
  height: 100%
  flex: 1
  padding: 10px

.sendBtn
  height: 100%
  width: auto
  min-width: 60px
  border-radius: var(--border-radius-sm)
  background-color: var(--color-surface-primary)
  border: none

.actionBtn
  margin-left: 5px
  margin-right: 5px
  padding: 10px 16px
  height: 100%
  display: flex
  align-items: center
  justify-content: center
  background-color: var(--color-surface-primary)
  border-radius: var(--border-radius-sm)
  border: none
  &:hover
    cursor: pointer

.actionBtnNoP
  margin-left: 5px
  margin-right: 5px
  height: 100%
  display: flex
  align-items: center
  justify-content: center
  background-color: var(--color-surface-primary)
  border-radius: var(--border-radius-sm)
  z-index: 1
  &:hover
    cursor: pointer

.actionIcon
  width: 16px
  height: 16px
  color: var(--color-text-primary)

.text-primary-icon
  color: var(--color-text-primary)

.actionWrap
  display: flex
  flex-direction: row
  top: 0px
  right: 15px
  align-items: center
  justify-content: flex-end
  width: 100%
  padding: 10px 15px

.filterItemH
  display: flex
  flex-direction: row
  align-items: center
