
.feedWrap
  display: flex
  align-items: flex-start
  flex-direction: column
  overflow-y: scroll
  background-color: var(--color-surface-secondary)
  border-radius: var(--border-radius-md)

.feedWrap::-webkit-scrollbar
  background-color: var(--color-surface-secondary)
  border-radius: var(--border-radius-md)

.feedWrap::-webkit-scrollbar-thumb
  background-color: var(--color-surface-primary)
  border-radius:  var(--border-radius-md)

.filterItemH
  display: flex
  flex-direction: row
  align-items: center

.terminalText
  display: flex
  align-items: center
  flex-direction: row

.messageTimeStamp
  opacity: 0.8
  margin-right: 20px
  font-size: 0.7rem
  margin-top: 7px
  margin-bottom: 7px
.divider
  width: 100%
  background-color: var(--color-divider)
  height: 1px

.itemWrap
  display: flex
  flex-direction: column
  width: 100%
.messageText
  margin-top: 7px
  margin-bottom: 7px
  white-space: pre-wrap
  word-break: break-all
