
.page-wrapper
  display: flex
  padding-left: 100px
  background-color: var(--color-background)
  &.is-in-mobile
    padding-left: 0
    .side-nav
      display: none
    .toggle-sidebar
      display: none

    .content-wrapper.hide-top-nav
      .top-nav
        display: none
      .page-content
        padding: 15px 0

  &.is-in-popup
    padding-left: 0
    .side-nav
      display: none
    .toggle-sidebar
      display: none

    .top-nav
      position: relative
      padding: 0 15px
    .page-content
      padding: 15px 0
      min-height: unset

.side-nav
  min-width: 100px
  max-width: 100px
  text-align: center
  background: var(--color-surface-primary)
  position: fixed
  left: 0
  top: 0
  height: 100%
  display: flex
  flex-direction: column
  z-index: 1000
  overflow-y: auto
  transition: all .2s ease-out
  border-right: 1px solid var(--color-divider)
  .sidebar-header
    flex: 0 0 50px
    display: flex
    align-items: center
    justify-content: center
    border-bottom: 1px solid var(--color-divider)
  ul
    li
      a
        display: block
        color: var(--color-text-primary)
        padding: 10px 5px
        margin: 4px
        border-radius: var(--border-radius-sm)
        text-align: center
        font-size: 0.85em
        &:hover, &[aria-expanded="true"]
          color: var(--color-text-primary)
        &:hover
          background: var(--color-hover)
        &.primary
          color: var(--color-primary)
        &.dropdown-toggle
          cursor: pointer
          padding-bottom: 20px
        i, ::v-deep .svg-inline--fa
          margin-right: 0
          display: block
          font-size: 1.4em
          margin-bottom: 5px
        ::v-deep .svg-inline--fa
          margin: 0 auto 5px
        .badge
          border-radius: 2em
          position: absolute
          top: -0.35em
          right: 2em
          padding: 0.25em 0.3em
          font-family: var(--default-font-family)
          font-size: .714rem
          font-weight: bold
          color: var(--color-on-danger)
          background-color: var(--color-danger)
          min-width: 1.6em
      &.active > a
        background: var(--color-hover)
      &.glowing
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px var(--color-primary), 0 0 70px var(--color-primary), 0 0 80px var(--color-primary), 0 0 100px var(--color-primary), 0 0 150px var(--color-primary)
    ul a
      font-size: 0.9em !important
      padding: 10px !important
    .dropdown-toggle::after
      top: auto
      bottom: 10px
      right: 50%
      -webkit-transform: translateX(50%)
      -ms-transform: translateX(50%)
      transform: translateX(50%)
  ul.components
    padding: 20px 0
  ::v-deep .logo-small
    width: 30px
    height: 30px
    color: var(--color-text-primary)
  .dropdown-toggle
    position: relative
    &::after
      display: block
      position: absolute
      top: 50%
      right: 20px
      transform: translateY(-50%)
  .side-nav-footer
    margin-top: auto
  .trim-text
    white-space: nowrap
    text-overflow: ellipsis
    display: inline-block
    overflow: hidden
    width: 100%
.content-wrapper
  width: 100%
.top-nav
  height: 50px
  background: var(--color-surface-primary) !important
  position: fixed
  top: 0
  left: 0
  width: 100%
  padding-left: calc(100px + 15px)
  padding-right: 32px
  z-index: 100
  box-shadow: var(--shadow-top-nav)
  justify-content: space-between

  ::v-deep .action-panel
    .action-btn
      box-sizing: border-box
      width: 36px
      padding-left: 0
      padding-right: 0
      @media (max-width: 768px)
        display: none

.is-in-mobile .top-nav
  padding-left: 15px
  padding-right: 15px
  background: var(--color-surface-primary) !important

.toggle-sidebar
  color: var(--color-text-primary)
  display: none

.page-content
  padding: calc(50px + var(--gap-between-blocks)) calc(var(--gap-between-blocks) - 15px) var(--gap-between-blocks)
  display: flex
  flex-direction: column
  min-height: 100vh

.content-overlay
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  z-index: 100
  background: rgb(0,0,0,.5)
  display: none

::v-deep .search-input input
  background-color: var(--color-surface-secondary)
  border-color: var(--color-surface-secondary)

.notification-dot
  height: 50%
  top: -9px
  right: -3px
  position: absolute
  width: 50%
  color: var(--color-danger)

@media (min-width: 769px)
  .content-wrapper
    &.hide-top-nav
      .top-nav
        display: none
      .page-content
        padding-top: 30px
        min-height: 100vh

@media (max-width: 768px)
  .page-wrapper
    padding-left: 0
    &:not(.collapsed) .content-overlay
      display: block
    &.collapsed
      .side-nav
        transform: translateX(-100px)

  .top-nav
    padding-left: 15px
    padding-right: 15px
    background: var(--color-surface-primary) !important

  .toggle-sidebar
    display: block

  .page-content
    padding: 15px 0
    padding-top: calc(50px + 15px)
    justify-content: start

::v-deep .dropdown-menu
  .dropdown-item
    padding: 0.25rem 0.75rem
    .clickable-area
      margin: -0.25rem -0.75rem
      padding: 0.25rem 0.75rem
    i, .custom-svg-icon
      width: 20px
      margin-right: .5rem
  .b-dropdown-text
    padding-left: 0.75rem
    padding-right: 0.75rem
