
body
  padding-bottom: 0

.wizard-container
  padding: 1.5rem 0.6rem 3rem

.feature-check
  color: var(--color-primary)

.feature-text
  margin-left: 0.5em

.authorization-successful
  padding-top: 6rem
  text-align: center

.logo-img
  max-height: 4em
  margin-bottom: 2em

.logo-icon
  max-height: 50px
  max-width: 50px
  margin: 0px 15px
  border-radius: 50%
  object-fit: cover

  &.obico
    max-width: 46px
    color: var(--color-primary)

.account-details
  background: var(--color-surface-secondary)
  border-radius: var(--border-radius-sm)
  padding: 15px
  margin-left: -15px
  margin-right: -15px

.footer-note
  margin: 3rem 0 -1.5rem
  text-align: right
