
.warning-block
  margin-bottom: var(--gap-between-blocks)

.available-printers,
.jusprin-feedback
  margin-top: var(--gap-between-blocks)
  &.full-width
    margin-top: 15px

.print-history
  &.full-width
    margin-top: 30px
  @media (max-width: 991px)
    margin-top: 30px

.file-name
  font-size: 1.25rem
  margin-bottom: 1rem
  border-bottom: 1px solid var(--color-divider)
  padding-bottom: 1rem

.file-info-line
  display: flex
  justify-content: space-between
  margin-bottom: 0.5rem

  i
    margin-right: 0.375rem

  .value
    font-weight: 700

.section-title
  font-size: 1.25rem
  margin-bottom: 0.5rem

.print-item
  margin-bottom: 10px
