
.buttons-row
  @media (max-width: 991px)
    margin-top: 2em
.heatmap-column
  display: flex
  flex-direction: column
  align-items: center
  .card
    background-color: var(--color-background)
    border-radius: var(--border-radius-lg)
.file-header
  display: flex
  align-items: center
  gap: 1rem
  padding-bottom: 5px
  .info
    flex: 1
  .icon
    flex: 0 0 2rem
    text-align: center
    *
      font-size: 2rem
.font-bold
  font-weight: bold
.first-layer-report-info-line
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottm: 6px
  padding: 6px 0
  gap: .5rem
  border-top: 1px solid var(--color-divider-muted)
  &:first-of-type
    border-top: none
  .label
    display: flex
    align-items: center
    flex: 1
    gap: .5rem
    line-height: 1.1
    .icon
      opacity: .5
      width: 1rem
      text-align: center
  .value
    font-weight: bold

.report-title
  padding-left: 15px

.feedback-button
  border: 1px solid var(--color-primary)
  display: block
  text-align: center
  border-radius: 17px
  padding: 8px
  color: var(--color-primary)
  background-color: transparent
  &:hover
    background-color: var(--color-hover)
hr
  width: 100%
  margin-top: 7px
  margin-bottom: 7px
.notes-block
  background-color: var(--color-background)
  padding: 1.5em
  border-radius: var(--border-radius-lg)

  display: flex
  flex-direction: column
  .title
    padding: 0 1em
  .description
    padding: 0 1em
    font-size: 0.9em
    p
      margin: 0
  .info
    padding: 0 1em
    font-size: 0.9em
    p
      margin: 0
    ul
      margin: 0 -15px
      li
        font-size: 0.9em


.file-block
  background-color: var(--color-background)
  padding: 1.5em
  border-radius: var(--border-radius-lg)
  display: flex
  flex-direction: column
  .title
    width:100%
    display: flex
    padding: 0 1em
    font-size: 1.125rem
    color: var(--color-text-primary)
    font-weight: medium
    span
      margin-left: .5em
    .icon
      flex: 1
      display: flex
      align-items: center
      i
        font-size: 2em
    .info
      flex: 15
      font-size: 1rem
      grid-column: span 1
      .file-size
        color: var(--color-divider)
    .button
      display: flex
      align-items: center
      justify-content: flex-end
  @media (max-width: 768px)
    grid-template-columns: 8% 60% 32%
  .first-layer-grade
    width:100%
    display: flex
    justify-content: space-between
    align-items: center
    padding: 0 1em
    .icon
      flex: 1
      display: flex
      align-items: center
      justify-content: center
    .name
      flex: 15
      @media (max-width: 768px)
        flex: 11
    .status
      display: flex
      justify-content: flex-end
  .first-layer-print-time
    width:100%
    display: flex
    justify-content: space-between
    padding: 0 1em
    grid-template-columns: 3% 67% 30%
    .icon
      flex: 1
      display: flex
      align-items: center
      justify-content: center

    .name
      flex: 18
      @media (max-width: 768px)
        flex: 15
    .status
      display: flex
      justify-content: flex-end

  &.bar-chart
    height: auto
    .chart-wrapper
      height: 210px
  @media (max-width: 768px)
    padding: 1.25em 1.5em


.dark-icon
  color: var(--color-divider)


$border-color: #dee2e6
.card-map
  border-radius: var(--border-radius-lg)
  background: white
  @media (min-width: 991px)
    max-width: 490px
  .card-body
    padding: 10%
    
.border-right
  border-right: 1px solid $border-color

.transparent-button
  background-color: transparent
  border-color: #007bff
  color: #007bff
  &:hover
    background-color: rgba(#007bff, 0.1)




@media (max-width: 768px)
  .file-block-title
    grid-template-columns: 7% 65% 28%

  @media (max-width: 1198px)
    .file-block-title
      grid-template-columns: 7% 65% 28%

.is-fullscreen
  position: fixed
  top: 0
  left: 0
  bottom: 0
  right: 0
  z-index: 9999
  background-color: var(--color-background)
  display: flex
  flex: 1
  order: 1
  ::v-deep .video-js
    height: 100vh !important
