
.wrapper
  padding-bottom: 1rem

.header
  margin-bottom: 1rem

.info-line
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottm: 6px
  padding: 6px 0
  gap: .5rem
  border-top: 1px solid var(--color-divider-muted)
  &.no-border
    border-top: none
  .label
    display: flex
    align-items: center
    flex: 1
    gap: .5rem
    line-height: 1.1
    .icon
      width: 1rem
      text-align: center
  .value
    font-weight: bold

.g-code-details
  padding: 0
  width: 100%

.actions
  margin-top: 1rem
  display: flex
  justify-content: center
  gap: 1rem

.progress-container
  font-weight: bold
  margin-bottom: 1rem
  display: flex
  gap: 1rem
  align-items: center
  .progress-bar-wrapper
    height: 12px
    flex: 1
    border-radius: 999px
    background-color: var(--color-background)
    position: relative
    top: 1px
    overflow: hidden
  .progress-bar-inner
    height: 100%
    // border-radius: 999px
    background-color: var(--color-primary)
.empty-state-text
  text-align: center
  font-size: 1.125rem
