
.fade-enter-active, .fade-leave-active
  transition: opacity 0.5s
.fade-enter, .fade-leave-to
  opacity: 0
.first-layer-info-column
  display: flex
  flex-direction: column
  justify-content: space-between
  height: 240px
  padding: 0
  @media (max-width: 576px)
    height: auto
    gap: 1em
  @media (max-width: 1198px) and (min-width: 991px)
    height: auto
    gap: 1em
.open-detailed-report-button
  width: 100%

.first-layer-report-block-video-container
  display: flex
  justify-content: flex-end
  padding-right: 0
  @media (max-width: 576px)
    padding: 0
    margin-top: 1em
    justify-content: center
  @media (max-width: 577px) and (max-width: 767px)
    padding: 0 0 0 5px
    justify-content: center
  @media (max-width: 1198px) and (min-width: 991px)
    margin-top: 1em
    padding: 0
    justify-content: center
.heatmap-image-container
  border-radius: var(--border-radius-lg)
  background: white
  padding: 32px
  @media (max-width: 576px)
    height: 393px
    width: 100%
    margin-top: 1.4em
  @media (max-width: 991px) and (min-width: 577px)
    width: 15em
  @media (max-width: 1198px) and (min-width: 992px)
    height: 393px
    width: 100%
    margin-top: 0.5em
  @media (min-width: 1199px)
    width: 15em

.heatmap-image
  border: 1px solid #cac8c8
  height: 176px

  @media (max-width: 576px)
    height: 330px
    width: 100%
  @media (max-width: 1198px) and (min-width: 991px)
    height: 100%
    width: 100%

.first-layer-print-time
  width:100%
  display: flex
  justify-content: space-between
  padding: 0 1em
  grid-template-columns: 3% 67% 30%
  .icon
    flex: 1
    display: flex
    align-items: center
    justify-content: center
    opacity: 0.5
  .name
    flex: 12
    @media (max-width: 768px)
      flex: 15
  .status
    display: flex
    justify-content: flex-end
.first-layer-grade
  width:100%
  display: flex
  justify-content: space-between
  align-items: center
  padding: 0 1em
  .icon
    flex: 1
    display: flex
    align-items: center
    justify-content: center
    opacity: 0.5
  .name
    flex: 7
    @media (max-width: 768px)
      flex: 11
  .status
    display: flex
    justify-content: flex-end

.print-info
  display: flex
  flex-direction: column
  gap: var(--gap-between-blocks)

.print-details
  overflow: hidden
  display: flex
  flex-direction: column
  gap: 10px
  .navigation-container
    display: flex
    justify-content: space-between
    align-items: center
    gap: 1rem
    margin: -1.5em
    margin-bottom: .5em
    padding: 1em 1.5em
    background-color: var(--color-surface-primary)
    .btn
      flex-shrink: 0
    .summary
      text-align: center
      @media (max-width: 576px)
        display: none
  .info-line
    display: flex
    align-items: center
    justify-content: space-between
    margin-bottm: 6px
    padding: 6px 0
    gap: .5rem
    border-top: 1px solid var(--color-divider-muted)
    &:first-of-type
      border-top: none
    .label
      display: flex
      align-items: center
      flex: 1
      gap: .5rem
      line-height: 1.1
      .icon
        opacity: .5
        width: 1rem
        text-align: center
    .value
      font-weight: bold

.first-layer-info-line
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottm: 6px
  padding: 6px 0
  gap: .5rem
  border-top: 1px solid var(--color-divider-muted)
  &:first-of-type
    border-top: none
  .label
    display: flex
    align-items: center
    flex: 1
    gap: .5rem
    line-height: 1.1
    .icon
      opacity: .5
      width: 1rem
      text-align: center
  .value
    font-weight: bold

.printer
  display: flex
  align-items: center
  gap: .7rem
  margin-bottom: var(--gap-between-blocks)
  .title
    font-weight: bold
  .info
    flex: 1
  .icon
    flex: 0 0 2rem
    text-align: center
    *
      font-size: 2rem

.time-lapse
  .title
    font-size: 1.5rem
    font-weight: normal
    margin-bottom: 1rem
  ::v-deep
    .card
      border-radius: var(--border-radius-lg)
      border: none
      background-color: var(--color-surface-secondary)
    .nav-pills .nav-link
      border-radius: var(--border-radius-sm)
    .nav-pills .nav-link.active
      background-color: var(--color-surface-primary)
    .card-header
      border-bottom: 1px solid var(--color-divider-muted)

.video-wrapper
  border-radius: var(--border-radius-md)
  overflow: hidden

  ::v-deep .video-js
    height: 500px !important

  ::v-deep video:focus-visible
    outline: none !important
    box-shadow: none !important
    border: none !important

.detective-footer
  margin-top: 30px

.feedback-section
  margin-top: 30px
  text-align: center

.about-feedback
  max-width: 600px
  display: inline-block
  margin-top: 30px

.detective-placeholder
  border-radius: var(--border-radius-md)
  overflow: hidden

.first-layer-video-wrapper.is-fullscreen
  flex: 1
  order: 1
  ::v-deep .video-js
    height: 100vh !important

.is-fullscreen
  position: fixed
  top: 0
  left: 0
  bottom: 0
  right: 0
  z-index: 9999
  background-color: var(--color-background)
  padding: 1rem
  display: flex

  .video-wrapper
    flex: 1
    order: 1
    ::v-deep .video-js
      height: calc(100vh - 2rem) !important

  .detective-footer
    flex: 0 0 200px
    padding: 0 1rem
    display: flex
    flex-direction: column
    justify-content: center
    position: relative

  @media (max-width: 996px)
    flex-direction: column

    .video-wrapper
      order: 0
      flex: 0 0 400px
      ::v-deep .video-js
        height: 400px !important

    .detective-footer
      flex: 1
      .feedback-section
        display: none

  &.original
    padding: 0

    .video-wrapper
      flex: 1 !important
      border-radius: 0
      ::v-deep .video-js
        height: 100vh !important
