
.gradient-fade-wrapper
  position: relative
  max-height: 8rem
  overflow: hidden
  transition: max-height 0.3s ease-out
  cursor: pointer

  &.is-unfaded
    max-height: 1000px

.fade-overlay
  position: absolute
  bottom: 0
  left: 0
  right: 0
  height: 3rem
  background: linear-gradient(transparent, var(--message-bg, white))
  pointer-events: none
