
.alert-wrapper
  border: 1px solid var(--color-divider)
  border-radius: var(--border-radius-md)
  display: flex
  align-items: center
  padding: 1em

  .icon
    color: var(--color-divider)

  .message
    margin: 0
    margin-left: 0.5em
    line-height: 1.3

  @media (max-width: 768px)
    font-size: 0.8rem
