
.card-img-top
  position: relative
  background-color: black

  .buttons-container
    position: absolute
    top: 0
    right: 0
    padding: 0.5rem
    background-color: rgba(0,0,0,0.7)

  a.action-btn
    padding: 0.5rem
    color: rgba(255,255,255,0.5)
    font-size: 1.5rem
    transition: all .3s ease-out
    &:hover
      color: rgba(255,255,255,0.9)
