
.jusprin-feedback
  svg
    width: 1.5em
    height: 1.5em
    color: gold

.feedback-question
  display: flex
  flex-direction: column
  align-items: center

  .question-text
    font-size: 1.2rem
    margin-right: 20px

  .icon-buttons
    display: flex
    justify-content: center
    gap: 30px

    .feedback-icon
      font-size: 1.3rem
      cursor: pointer
      transition: color 0.3s ease

      &:hover
        opacity: .8

      &.selected
        color: var(--color-primary)

.text-feedback
  margin: 0 auto

  .form-group
    text-align: left

  textarea
    resize: vertical

  .text-right
    text-align: right


.fade-enter-active, .fade-leave-active
  transition: opacity 0.5s

.fade-enter, .fade-leave-to
  opacity: 0
