
.nav-btn
  display: inline-block
  color: var(--color-text-primary)
  cursor: pointer

.wrapper
  padding-bottom: 1.5rem
  min-height: 260px
  display: flex
  justify-content: center
  align-items: center

.home-menu
  display: flex
  flex-direction: row
  justify-content: center
  gap: 1rem
  width: 100%
  flex-wrap: wrap
  @media (max-width: 510px)
    gap: .5rem

.menu-button
  width: calc((100% - 3rem) / 3)
  flex-shrink: 0
  padding: 1.5rem 1rem
  background-color: var(--color-background)
  color: var(--color-text-primary)
  border: none
  border-radius: var(--border-radius-md)
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  gap: .5rem
  cursor: pointer
  @media (max-width: 510px)
    width: calc((100% - 2rem) / 3)
    padding: 1rem 0.5rem
  &:hover
    opacity: .8
  &:disabled
    opacity: .5
    cursor: not-allowed
  i, svg
    font-size: 1.75rem
    @media (max-width: 510px)
      font-size: 1.2rem
.icon
  --icon-size: 42px
  width: var(--icon-size)
  height: var(--icon-size)
  color: var(--color-text-primary)
  &.move-xy
    --icon-size: 38px
  &.extruder
    --icon-size: 36px
  @media (max-width: 510px)
    --icon-size: 24px !important
.title
  font-size: 0.875rem
  line-height: 1.2


.control-panel
  width: 100%
  .main
    display: flex
    align-items: center
    justify-content: center
    gap: 2rem
    margin-bottom: 2rem
    @media (max-width: 510px)
      gap: 1rem
  .toggles
    display: flex
    flex-direction: column
    gap: .5rem
  .pill
    padding: .25rem
    width: 3.5rem
    background-color: var(--color-secondary)
    color: var(--color-on-secondary)
    border-radius: 999px
    font-size: 0.875rem
    text-align: center
    cursor: pointer
    &.active
      background-color: var(--color-primary)
      color: var(--color-on-primary)
  .xy-move, .z-move
    width: 146px
    height: 146px
    border-radius: var(--border-radius-md)
    background-color: var(--color-background)
    color: var(--color-text-primary)
    position: relative
    .left, .right, .up, .down, .home
      position: absolute
      width: 42px
      height: 42px
      font-size: 1.25rem
      text-align: center
      line-height: 42px
      margin: auto
      left: 0
      right: 0
      top: 0
      bottom: 0
      cursor: pointer
      &:hover
        opacity: .8
    .left
      right: unset
    .right
      left: unset
    .up
      bottom: unset
    .down
      top: unset

  .z-move
    width: 60px
  .additional
    display: flex
    justify-content: center
    gap: 1rem
  .control-btn
    display: flex
    align-items: center
    gap: .5rem
    cursor: pointer
    background-color: var(--color-background)
    border-radius: var(--border-radius-sm)
    color: var(--color-text-primary)
    padding: .5rem 1rem
    font-size: 0.875rem
    &:hover
      opacity: .8

.current-offset
  display: flex
  gap: 1rem
  border: 1px solid var(--color-divider-muted)
  border-radius: var(--border-radius-sm)
  color: var(--color-text-primary)
  padding: .5rem 1rem
  font-size: 0.875rem
  .value
    font-weight: bold

.main-buttons
  display: flex
  flex-direction: column
  gap: 1rem
  .control-btn
    font-size: 1rem
    border-radius: var(--border-radius-md)
    padding: .625rem 1.5rem

.tool-select
  width: 80%

.tune-printer
  display: flex
  flex-direction: column
  align-items: center
  gap: .825rem

  .controls-title
    font-size: 1rem
    width: 100%
    display: flex
    gap: .375rem
  .controls
    width: 100%
    display: flex
    gap: .75rem
    margin-bottom: 1rem

    .off
      flex-shrink: 0
      width: 100px

    .custom
      border-radius: 100px
      overflow: hidden
      flex: 1

    ::v-deep .input-group-text
      background-color: var(--color-divider)
      color: var(--color-text-primary)

    @media (max-width: 510px)
      flex-direction: column
      .off
        width: 100%

  .info-block
    width: 100%

.power-control
  .power-item
    margin: 1rem 0
    display: flex
    flex-direction: column
    gap: .5rem
  .title
    display: flex
    justify-content: space-between
    font-size: 1rem
    font-weight: bold
  .bulk-actions
    display: flex
    gap: 1rem
    margin: 2rem 0
    button
      flex: 1
