


.clickable {
  &:hover{
    cursor: pointer;
  }
}
.form-container{

  .printer-list{
    display:flex;
    gap:20px;
    .printer-item{
      .img-wrap{
        padding:20px;
        background:#485B71;
        border-radius:6px;
        img{
          width:100px;
          height:auto;
        }
      }
      .item-label{
        margin-top:3px;
        text-align:center;
      }
    }
  }
}


.wizard-card-list{
  margin-top:40px;

  .wizard-card-horizontal{
    margin-top:20px;
    display:flex;
    align-items:center;
    gap:30px;
    background-color: var(--color-surface-primary);
    border-radius: var(--border-radius-md);
    padding:20px;
    cursor: pointer;

  .img-wrap{
    img{
      width: 5em;
    }
    .img-tip{
      display:flex;
      justify-content:space-between;
      img{
        width:2em;
      }
    }
  }
  .text-wrap{
    h5{
      margin-top:10px;
      color:#AAACB0;
      font-size:16px;
    }
  }
}
}

