
@import '../../../../styles/theme';
@import '../../../../styles/mixin';

// Upgrade Modal Styles
.upgrade-modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-overlay);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1050;
}

.upgrade-modal {
  background-color: var(--color-surface-secondary);
  border-radius: var(--border-radius-lg);
  padding: 2rem;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: var(--shadow-top-nav);

  @include respond-below(md) {
    padding: 1.5rem;
    margin: 0 1rem;
    width: calc(100% - 2rem);
  }

  .close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;

    &:hover, &:focus {
      background-color: var(--color-hover) !important;
      color: var(--color-text-primary) !important;
    }
  }

  .modal-title {
    color: var(--color-text-primary);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0 0 1.5rem 0;
    text-align: center;

    @include respond-below(md) {
      font-size: 1.25rem;
      margin-bottom: 1rem;
    }
  }

  .user-info {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 400px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;
      margin-bottom: 1.5rem;
    }

    .user-details {
      .user-name {
        color: var(--color-text-primary);
        font-size: 1.25rem;
        font-weight: 600;
        margin: 0 0 0.25rem 0;

        @include respond-below(md) {
          font-size: 1.125rem;
        }
      }

      .user-email {
        color: var(--color-text-secondary);
        font-size: 0.875rem;
        text-decoration: underline;
      }
    }

    .plan-badge {
      color: var(--color-text-primary);
      padding: 0.25rem 0.75rem;
      border-radius: var(--border-radius-sm);
      border: 1px dashed var(--color-divider);
      font-size: 0.75rem;
      font-weight: 600;
      text-transform: uppercase;

      &.pro-badge {
        color: var(--color-primary);
        border: 1px dashed var(--color-primary);
      }
    }
  }

  .body-section {
    margin-bottom: 1rem;

    @include respond-below(md) {
      margin-bottom: 1.5rem;
    }

    .ai-credits-header {
      display: flex;
      align-items: baseline;
      gap: 0.5rem;
      margin-bottom: 1rem;

      .credit-icon {
        width: 1.5rem;
        height: 1.5rem;
        flex-shrink: 0;
      }
    }

    .progress-bar-container {
      width: 100%;
      height: 8px;
      background-color: var(--color-surface-primary);
      border-radius: var(--border-radius-xs);
      overflow: hidden;
      border: 1px solid var(--color-divider-muted);

      .progress-bar {
        height: 100%;
        background-color: var(--color-primary);
        border-radius: var(--border-radius-xs);
        transition: width 0.3s ease;

        &.credit-success {
          background-color: var(--color-success);
        }

        &.credit-warning {
          background-color: var(--color-warning);
        }

        &.credit-exhausted {
          background-color: var(--color-danger);
        }
      }
    }

    .progress-info {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 0.5rem;
      gap: 1rem;

      @media (max-width: 400px) {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.25rem;
      }

      .usage-text {
        color: var(--color-text-primary);
        font-size: 0.875rem;
        font-weight: 500;
        flex: 1;
        min-width: 0;
      }

      .reset-date {
        color: var(--color-text-secondary);
        font-size: 0.75rem;
        font-weight: 400;
        flex-shrink: 0;
        white-space: nowrap;
      }
    }

    .modal-section-title {
      color: var(--color-text-primary);
      font-size: 1.5rem;
      font-weight: 600;
      margin-bottom: 0.5rem;
      margin-top: 2rem;
      text-decoration: none;
    }

    .section-description {
      color: var(--color-text-secondary);
      font-size: 0.875rem;
      margin-bottom: 1rem;
    }

    .upgrade-option {
      border: 1px solid var(--color-divider);
      border-radius: var(--border-radius-md);
      padding: 1rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 1rem;

      @media (max-width: 400px) {
        flex-direction: column;
        align-items: stretch;
        gap: 1rem;
        padding: 0.875rem;
      }

      .upgrade-details {
        flex: 1;
        min-width: 0;

        @media (min-width: 401px) {
          min-width: 200px;
        }

        .upgrade-title {
          color: var(--color-text-primary);
          font-size: 1.2rem;
          font-weight: 500;
          margin-bottom: 0.25rem;
          display: flex;
          align-items: center;
          gap: 0.5rem;

          .discount {
            background-color: var(--color-primary);
            color: var(--color-on-primary);
            padding: 0.125rem 0.5rem;
            border-radius: var(--border-radius-lg);
            font-size: 0.75rem;
            font-weight: 600;
          }
        }
      }

      .upgrade-button {
        padding: 0.75rem 2rem;
        font-size: 0.875rem;
        font-weight: 600;
        white-space: nowrap;
        flex-shrink: 0;

        @media (max-width: 400px) {
          width: 100%;
          white-space: normal;
        }

        &:hover {
          background-color: var(--color-primary-hover);
        }
      }
    }

    .subscription-info {
      border: 1px solid var(--color-primary);
      border-radius: var(--border-radius-md);
      padding: 1rem;
      background-color: var(--color-surface-primary);

      .subscription-details {
        .subscription-plan {
          color: var(--color-primary);
          font-size: 1.1rem;
          font-weight: 600;
          margin-bottom: 0.5rem;
          display: flex;
          align-items: center;
          gap: 0.5rem;

          i {
            color: var(--color-primary);
          }
        }
      }
    }
  }

  .credit-exhausted-banner {
    background-color: var(--color-danger);
    color: white;
    padding: 1rem;
    border-radius: var(--border-radius-md);
    margin-bottom: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.75rem;
    font-weight: 700;
    font-size: 1rem;
    text-align: center;
    justify-content: center;
    border: 2px solid var(--color-danger);
    animation: pulse-danger 2s ease-in-out infinite;

    i {
      font-size: 1.25rem;
      flex-shrink: 0;
    }

    span {
      font-weight: 700;
    }

    @media (max-width: 400px) {
      font-size: 0.875rem;
      padding: 0.875rem;
      margin-bottom: 1rem;
    }
  }

  @keyframes pulse-danger {
    0%, 100% {
      box-shadow: 0 0 0 0 rgba(var(--color-danger-rgb, 220, 53, 69), 0.4);
    }
    50% {
      box-shadow: 0 0 0 8px rgba(var(--color-danger-rgb, 220, 53, 69), 0);
    }
  }
}
