
.item
  display: flex
  align-items: center
  padding: .6em 1em
  border-bottom: 1px solid var(--color-divider-muted)
  &:last-child
    border-bottom: none
  &:hover
    cursor: pointer
    background-color: var(--color-hover)
  &.disabled
    opacity: .5
    &:hover
      cursor: default
      background-color: transparent
  &.move-modal
    .item-info
      .size
        text-align: right

  .item-info
    display: flex
    width: 100%
    overflow: hidden
    flex: 1
    font-size: 0.875rem
    color: var(--color-text-secondary)
    & > div
      flex: 1
      margin-left: 30px
      &:first-child
        font-size: 1rem
        color: var(--color-text-primary)
        margin-left: 0

    .thumbnail-wrapper
      flex: 0 0 32px
      display: inline-flex
      height: 32px
      border-radius: var(--border-radius-xs)
      background-color: var(--color-surface-primary)
      overflow: hidden
      align-items: center
      justify-content: center
      .thumbnail
        width: 100%
        height: 100%
        img
          height: 100%
          width: auto
      &.folder
        background: none
        font-size: 1.25em

    .filename
      display: flex
      align-items: center
      gap: .5rem
      text-overflow: ellipsis
      overflow: hidden
      white-space: nowrap
      width: 100%
      flex: 3

  .remove-button
    width: 30px
    height: 30px
    text-align: center
    line-height: 30px
    border-radius: 50%
    transition: background-color .2s ease-out
    &:hover
      background-color: var(--color-danger)
      color: var(--color-on-primary)
      cursor: pointer

  @media (max-width: 768px)
    &
      margin: 0 -16px

    .item-info
      flex-direction: column
      align-items: flex-start
      & > div
        margin-left: 0

      .size::before
        content: "Size: "
      .uploaded::before
        content: "Uploaded: "
      .created::before
        content: "Created: "
      .last-printed::before
        content: "Last print: "

.circle-indicator
  --size: 6px
  width: var(--size)
  height: var(--size)
  border-radius: var(--size)
  display: inline-block
  margin-left: 5px
  position: relative
  bottom: 1px
  background: var(--color-text-secondary)
  &.cancelled
    background: var(--color-danger)
  &.finished
    background: var(--color-success)

.checkbox-wrapper
  display: flex
  align-items: flex-start
  justify-content: flex-end
  margin-left: -.5rem
  margin-right: .5rem
  ::v-deep .custom-checkbox .custom-control-label::before
    border-radius: var(--border-radius-xs)
    border-color: var(--color-divider)
  &.isSelected
    ::v-deep .custom-checkbox .custom-control-label::before
      border-color: #00C4B4
  @media (max-width: 576px)
    display: none
