
.octoprint-image {
  width: 100%; 
  margin-bottom: 1em;
}
.form-container {
  .klipper-pre-install-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      width: 150px;
    }

    .text-wrap {
      width: 70%;
      margin-top:20px;
      h4 {
        color: #EBEBEB;
        font-size: 22px;
        line-height:34px;
        margin-top: 1em;
        font-weight: 600;
      }
      ol{
        padding-left:15px;
        margin-top:8px;
        li{
          margin-top:10px;
        }
      }
      .find-printer-link {
        color: var(--color-primary);
        i {
          transform: rotate(45deg);
        }
        a {
          margin-right: 4px;
          font-weight: 600;
          text-decoration: underline;
        }
      }
    }
  }
  .octo-wrap{
    img{
      width:100%;
    }
  }
  .klipper-wrap{
    color:#EBEBEB;
    font-size:20px;
  }
  .printer-item{
    display:flex;
    justify-content: space-between;
    align-items:center;
    gap:40px;

    .printer-left{
      flex:1;
      display:flex;
      flex-direction: column;
      align-items:center;

      .img-wrap{
        margin-top:20px;
        background-color: var(--color-surface-primary);
        border-radius:var(--border-radius-lg);
        padding:20px;
        img{
          width:300px;
        }
      }
    }
    .line{
      align-self: stretch;
      width:1px;
      background-color: #fff;

    }
    .printer-right{
      flex:1;
      p{
        margin-bottom:30px;
        font-size:20px;
      }
    }
  }
  .button-wrap{
    margin-top:80px;
    .back{
      cursor: pointer;
    }
  }

}
