
.temp-item
  display: flex
  width: 100%
  background-color: var(--color-background)
  padding: .8125rem
  border-radius: var(--border-radius-md)
  gap: 1rem
  align-items: center
  &.editable
    cursor: pointer
.icon
  width: 36px
  height: 36px
  display: flex
  align-items: center
  justify-content: center
  background-color: var(--color-primary)
  color: var(--color-on-primary)
  border-radius: var(--border-radius-sm)
  svg
    width: 20px
    height: 20px
    color: var(--color-on-primary)

.value-wrapper
  margin-left: auto
  display: flex
  gap: .375rem
.value
  font-weight: bold
.target
  color: var(--color-text-secondary)
