
.wrapper
  display: flex
  flex-direction: column
  gap: 1rem

.item
  display: flex
  align-items: center
  gap: .5rem
  border-bottom: 1px solid var(--color-divider-muted)
  padding: .5rem 0
  .handle
    font-size: 1.25rem
    color: var(--color-text-secondary)
    cursor: move
  .title
    flex: 1
  &.ghost
    opacity: .1
