
.wrapper
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  padding-bottom: 2rem
  gap: 1rem
  @media (max-width: 480px)
    height: auto

.error-container, .warning-container
  width: 100%
  background-color: var(--color-danger)
  color: var(--color-on-danger)
  padding: 1rem
  border-radius: var(--border-radius-sm)
  text-align: center
  font-weight: normal
  margin-bottom: 1rem
  .title
    font-size: 1rem
    font-weight: bold
    margin-bottom: .5rem
  p
    font-size: 1rem
    font-weight: normal
  &.warning-container
    background-color: var(--color-warning)
    color: var(--color-on-warning)

p
  margin: 0
  font-size: 1.125rem
  text-align: center

.buttons
  display: flex
  align-items: center
  justify-content: center
  gap: 1rem
  margin-top: .5rem
  flex-wrap: wrap

.big-icon
  font-size: 2rem
  &.warning
    color: var(--color-warning)
  &.danger
    color: var(--color-danger)

.custom-svg-icon
  height: 1.125rem
  width: 1.125rem
