
.muted-alert
  margin-bottom: var(--gap-between-blocks)

.stats-block
  background-color: var(--color-surface-secondary)
  padding: 1.75em 2.25em
  border-radius: var(--border-radius-lg)
  height: 280px
  display: flex
  flex-direction: column
  &.bar-chart
    height: auto
    .chart-wrapper
      height: 210px
  @media (max-width: 768px)
    padding: 1.25em 1.5em

.stats-block-title
  font-size: 1.125rem
  color: var(--color-text-primary)
  font-weight: medium
  span
    display: inline-block
    margin-left: .5em
  i
    color: var(--color-divider)

.print-time
  justify-content: space-between
  .title
    font-size: 1.125rem
    color: var(--color-text-secondary)
  .value
    font-size: 1.5rem
    color: var(--color-text-primary)
  .other-print-time-numbers
    display: flex
    .divider
      width: 1px
      background-color: var(--color-divider)
      margin: 0 2rem
    .info
      .title
        font-size: .875rem
      .value
        font-size: 1.125rem

.total-prints
  .chart-wrapper
    flex: 1
    display: flex
    justify-content: space-between
    align-items: center
    gap: 1rem
  .legend
    display: flex
    flex-direction: column
    justify-content: center
  .line
    display: flex
    align-items: center
    gap: .3rem
    margin: .25rem 0
    @media (max-width: 768px)
      font-size: .875rem
  .square
    width: 1.125rem
    height: 1.125rem
    border-radius: var(--border-radius-xs)
    margin-right: .2rem
    &.success
      background-color: var(--color-success)
    &.danger
      background-color: var(--color-danger)
  .value
    font-weight: bold

.filament-used-groups
  .title-group
    display: inline-flex
    align-items: center
    gap: .875rem
    @media (max-width: 768px)
      margin-left: 0.5rem
      flex-direction: column
      align-items: flex-start
      gap: 0
      .divider
        display: none
      span
        margin-left: 0
    .divider
      width: 1px
      background-color: var(--color-divider)
      height: 1.25rem
