
.file
  display: flex
  flex-direction: column

.file-header
  display: flex
  align-items: center
  gap: 1rem
  .info
    flex: 1
  .icon
    flex: 0 0 2rem
    text-align: center
    *
      font-size: 2rem
  .thumbnail, .thumbnail-placeholder
    width: 100px
    height: 100px
    border-radius: var(--border-radius-md)
    background-color: var(--color-surface-primary)
    display: flex
    align-items: center
    justify-content: center
    overflow: hidden
    img
      height: 100%
      width: auto
      border-radius: 8px
  .title
    font-weight: bold
    font-size: 1.25rem
  .thumbnail-placeholder
    position: relative
    overflow: visible
    svg
      width: 3rem
      height: 3rem
      color: var(--color-background)
    .help
      position: absolute
      top: 4px
      right: 8px

  &.compact
    gap: .7rem
    .thumbnail
      width: 52px
      height: 52px
    .title
      font-size: 1rem

.line
  display: flex
  align-items: center
  justify-content: space-between
  margin-bottm: 6px
  padding: 6px 0
  border-top: 1px solid var(--color-divider-muted)
  gap: .5rem

  .label
    display: flex
    align-items: center
    flex: 1
    gap: .5rem
    line-height: 1.1
    .icon
      opacity: .5
      width: 1rem
      text-align: center

  .value
    font-weight: bold
    text-align: right
