
.wrapper
  padding-bottom: 1rem

.header
  position: relative
  display: flex
  justify-content: center
  align-items: center
  @media (max-width: 510px)
    flex-direction: column

  .overlay-info
    position: absolute
    left: 0
    bottom: 0
    right: 0
    top: 0
    display: flex
    align-items: center
    justify-content: center
    .muted-alert
      position: relative
      z-index: 2
    &::before
      content: ''
      position: absolute
      left: 0
      bottom: 0
      right: 0
      top: 0
      background: var(--color-surface-secondary)
      z-index: 1
      opacity: 0.97

.controls
  margin-top: 1rem
  .line
    display: flex
    justify-content: space-between
    align-items: center
    padding: 0.5rem 0
    border-bottom: 1px solid var(--color-divider-muted)
    &:last-of-type
      border-bottom: none
    .label
      margin-bottom: 0
      font-size: 1rem
      .text-muted
        font-size: 0.875rem

.failure-detected-message
  margin-top: 1rem
  display: flex
  gap: 1.5rem
  align-items: center
  @media (max-width: 510px)
    flex-direction: column
    gap: 0.75rem
  .warning-message
    background-color: var(--color-warning)
    color: var(--color-on-warning)
    padding: 0.625rem 1rem
    border-radius: var(--border-radius-sm)
    flex: 1
    text-align: center
    font-weight: bold
    font-size: normal
    i
      margin-right: 0.25rem

.dh-balance-wrapper
  position: absolute
  top: 8px
  right: 12px
.btn.hours-btn
  padding-left: 0
  padding-right: 0
  width: 36px
  position: relative
  color: var(--color-text-primary)
  .badge
    position: absolute
    left: 18px
    top: 4px
    border-radius: var(--border-radius-sm)
    background-color: var(--color-primary)
    height: auto
    font-size: .625rem
.custom-svg-icon
  height: 1.3rem
  width: 1.3rem
