
.print-item
  margin-bottom: 10px

.prints-summary
  margin-bottom: var(--gap-between-blocks)
  border: 1px solid var(--color-divider)
  border-radius: var(--border-radius-lg)
  padding: 1.5rem 2rem
  display: flex
  justify-content: space-between
  align-items: center
  .summary-item
    display: flex
    align-items: center
    gap: 1rem
  .icon
    font-size: 1.5rem
    color: var(--color-divider)
  .value
    font-weight: bold
  .btn
    display: inline-flex
    gap: .5rem
    align-items: center
    justify-content: center
    .fa-arrow-right
      font-size: .8em

  @media (max-width: 768px)
    flex-direction: column
    align-items: normal
    gap: .25rem
    padding: 1rem

    .summary-item
      gap: 0
      .icon
        font-size: 1rem
        width: 2rem
        text-align: center
      .info
        flex: 1
        display: flex
        justify-content: space-between

    .btn-wrapper
      text-align: center
    .btn
      margin-top: 1rem
      font-size: .875rem

.actions-with-selected-desktop
  display: flex
  align-items: center
  .label
    cursor: pointer
  ::v-deep .custom-checkbox .custom-control-label::before
    border-radius: var(--border-radius-xs)
